@charset "UTF-8";
@import "https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;700&display=swap";

/* LNC SVG font by LANACO | V1.2 | release date: 08.08.2019 */
@font-face {
  font-family: "lnclines";
  src: url("lnclines.eot");
  src: url("lnclines.eot?#iefix") format("embedded-opentype"),
    url("lnclines.woff") format("woff"), url("lnclines.ttf") format("truetype"),
    url("lnclines.svg#lncines") format("svg");
  font-weight: normal;
  font-style: normal;
}

:root {
  /* BASE COLORS */
  --color-base-blue: #00537a;
  --color-base-blue-darker: #003b57;
  --color-base-blue-lighter: #006999;
  --color-base-gray: #a2a5ab;
  --color-base-gray-darker: #777a80;
  --color-base-gray-lighter: #dee1e6;
  --color-base-green: #2a963c;
  --color-base-green-darker: #21732f;
  --color-base-green-ligher: #3eb051;
  --color-base-red: #e0223e;
  --color-base-red-darker: #e3566b;
  --color-base-red-ligher: #9c1f32;
  --color-base-dirty-white: #fcfcfc;
  --color-base-white: white;
  --color-base-backgroud: #dceff5;

  /* BASE FONTS */
  --font-base-ubuntu: "Ubuntu", sans-serif;
  --font-size-base: 1em;

  /* DISABLED */
  --disabled-bg-color: #dedede;
  --disabled-text-color: #666666;

  /* TRANSITIONS */
  --transition-base-duration: 400ms;
}

.baseContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1px;
  background-color: inherit;
  background: inherit;
  color: inherit;
  width: max-content;
  font-family: var(--font-base-ubuntu);
}

.baseContainerWithSideLabel {
  width: min-content;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 1px;
  background-color: inherit;
  background: inherit;
  color: inherit;
  width: max-content;
  font-family: var(--font-base-ubuntu);
}

.baseContainerWithSideLabel .baseLabel {
  width: auto;
}

.baseLabel {
  font-size: 0.8em;
  font-weight: 400;
  color: var(--color-base-gray-darker);
  width: 100%;
  display: flex;
  justify-content: left;
  align-items: flex-end;
  height: 1em;
  min-height: 1em;
}

.baseInputHolder {
  border: 0px;
  height: 2em;
  min-height: 2em;
  width: 100%;
}

.baseInputHolder > div {
  min-height: 100%;
}

.baseInputHolder > div > div {
  min-height: 100%;
}

.baseErrorText {
  font-size: 0.8em;
  font-weight: 400;
  color: var(--color-base-red);
  width: 100%;
  display: flex;
  justify-content: left;
  align-items: flex-start;
  height: 1em;
  min-height: 1em;
}

/*----------------- ICONS AND FONTS ------------------------*/
.lnc {
  font-family: "lnclines";
  font-size: 0.7em;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.ubuntuFont {
  font-family: var(--font-base-ubuntu);
}

